import React, { Component } from 'react';
import { Row, Col, Menu, Icon, Form } from 'antd';
import './style.scss';

@Form.create({ name: 'formSerchIndividual' })
class MenuTabTop extends Component {
  state = {};

  handleClick = e => {
    // console.log('click ', e);
    this.setState({
      current: e.key,
    });
    if (e.key === 'enteringInfo') {
      console.log('render');
    }
  };
  render() {
    return (
      <Form onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={24}>
            <Menu onClick={this.handleClick} selectedKeys={[this.state.current]} mode="horizontal">
              <Menu.Item key="saveChange">
                <Icon type="setting" />
                Lưu thay đổi
              </Menu.Item>
              <Menu.Item key="updateTotal">
                <Icon type="highlight" theme="twoTone" twoToneColor="#ff3333" />
                Cập nhật sỉ số lớp
              </Menu.Item>
              <Menu.Item key="updateShift">
                <Icon type="highlight" theme="twoTone" twoToneColor="#ff3333" />
                Cập nhật ca học
              </Menu.Item>
              <Menu.Item key="divideRoome">
                <Icon type="printer" theme="twoTone" twoToneColor="#6699ff" />
                Phân phòng tự động
              </Menu.Item>
            </Menu>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default MenuTabTop;



