import React, { Component } from 'react';
import { Row, Col, Divider, Table, Select } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import { setTableClassList, setShiftSchool, setClassRoom } from 'dump/data';
import MenuTabTop from './MenuTabTop';

class TableClassList extends Component {
  constructor(props) {
    super(props);
    this.state = {};
    const { Option } = Select;

    this.columns = [
      {
        title: 'Lớp',
        dataIndex: 'lop',
        key: 'lop',
      },
      {
        title: 'Số SV',
        dataIndex: 'soSV',
        key: 'soSV',
      },
      {
        title: 'Ca học dự kiến',
        dataIndex: 'ca_hoc_du_kien',
        render: () =>
          setShiftSchool.length >= 1 ? (
            <Select style={{ width: '100%' }}>
              {setShiftSchool.map(item => (
                <Option value={item.code}>{item.name}</Option>
              ))}
            </Select>
          ) : null,
      },
      {
        title: 'Phòng học',
        dataIndex: 'phong_hoc',
        render: () =>
          setClassRoom.length >= 1 ? (
            <Select style={{ width: '100%' }}>
              {setClassRoom.map(item => (
                <Option value={item.code}>{item.name}</Option>
              ))}
            </Select>
          ) : null,
      },
      {
        title: 'Hệ đào tạo',
        dataIndex: 'heDaoTao',
        key: 'heDaoTao',
      },
      {
        title: 'Tên khoa',
        dataIndex: 'tenKhoa',
        key: 'tenKhoa',
      },
      {
        title: 'Khóa học',
        dataIndex: 'khoaHoc',
        key: 'khoaHoc',
      },
      {
        title: 'Tên ngành',
        dataIndex: 'tenNganh',
        key: 'tenNganh',
      },
    ];

    this.rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
  }
  render() {
    return (
      <div>
        <Divider>Danh sách lớp học</Divider>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSearch />
              <BtnClear />
            </div>
          </Col>
        </Row>
        <Row style={{ textAlign: 'right' }}>
          <Col span={24}>
            <MenuTabTop />
          </Col>
        </Row>
        <Row>
          <Col>
            <Table
              bordered
              rowSelection={this.rowSelection}
              dataSource={setTableClassList}
              columns={this.columns}
            />
          </Col>
        </Row>
      </div>
    );
  }
}

export default TableClassList;



